<template>
  <div class="product">
    <div class="product__title">{{ shopName }}</div>
    <div class="product__item" v-for="item in productList" :key="item._id">
      <img class="product__item__img" :src="item.imgUrl" alt="">
      <div class="product__item__detail">
        <div class="product__item__title">{{ item.name }}</div>
        <div class="product__item__price">
            <span class="product__item__single">
              <span class="product__item__yen">&yen;</span>{{ item.price }} x {{ item.count }}
            </span>
          <span class="product__item__total">
              <span class="product__item__yen">&yen;</span>{{ (item.price * item.count).toFixed(2) }}
            </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useRoute } from 'vue-router'
import { useCommonCartEffect } from '@/effects/cartEffects'

export default {
  name: 'ProductList',
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { cartList } = useCommonCartEffect()
    const shopName = cartList?.[shopId].shopName
    const productList = cartList?.[shopId].productList

    return {
      shopName,
      productList
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";
@import "../../style/mixins";

.product {
  background: $bgColor;
  border-radius: 4px;
  margin: .16rem .18rem .18rem;
  padding: .16rem;

  &__title {
    line-height: .22rem;
    font-size: .16rem;
    color: $content-fontColor;
    font-weight: bold;
    padding-bottom: .16rem;
  }

  &__item {
    display: flex;
    padding-bottom: .16rem;

    &__img {
      width: .46rem;
      height: .46rem;
      margin-right: .16rem;
    }

    &__detail {
      flex: 1;
      overflow: hidden;
    }

    &__title {
      line-height: .2rem;
      font-size: .14rem;
      font-weight: bold;
      color: $content-fontColor;
      @include ellipsis;
      margin-bottom: .06rem;
    }

    &__price {
      line-height: .2rem;
      font-size: .14rem;
    }

    &__single {
      color: $highlight-fontColor;
    }

    &__total {
      float: right;
    }

    &__yen {
      font-size: .1rem;
    }
  }
}
</style>
