<template>
  <div class="order">
    <div class="order__price">实付金额&nbsp;&yen;<span class="order__price__number">{{ price }}</span></div>
    <div class="order__btn">提交订单</div>
  </div>
</template>

<script>
import { useRoute } from 'vue-router'
import { useCommonCartEffect } from '@/effects/cartEffects'

export default {
  name: 'Order',
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { cartList } = useCommonCartEffect()
    let price = 0
    const productList = cartList?.[shopId].productList
    for (const key in productList) {
      const product = productList[key]
      product.check && (price += product.price * product.count)
    }
    return {
      price: price.toFixed(2)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";

.order {
  position: fixed;
  bottom: 0;
  line-height: .49rem;
  width: 100%;
  display: flex;

  &__price {
    flex: 1;
    font-size: .14rem;
    color: $content-fontColor;
    text-indent: .24rem;

    &__number {
      font-size: .16rem;
      color: #151515;
      font-weight: bold;
    }
  }

  &__btn {
    width: .98rem;
    background: #4FB0F9;
    color: $bgColor;
    font-size: .14rem;
    text-align: center;
  }
}
</style>
